@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,700&display=swap);
body{background:#b0a8b9;font-family:"Roboto",sans-serif;margin:0 auto;text-align:center;max-width:70vh}@media (max-width: 1200px){body{max-width:50vh}}.header{color:#005893}.content{display:flex;margin:4rem 0}.card-wrapper{display:flex;flex-wrap:wrap;justify-content:space-between}.counter{color:#005893;font-size:36px}

.card{background:#7fd1ae;border:1px solid #b2e0ff;border-radius:5px;cursor:pointer;height:12rem;margin:5px;width:10rem}.card:hover{background:#98f5cd}@media (max-width: 320px){.card{height:10rem}}.title{margin:2em}

